@use "base/variables" as v;
@use "base/mixins" as m;

.proceso-cafe {
    margin-bottom: v.$separacion;

    &:last-of-type{
        margin-bottom: 0;
    }
    
	@include m.tablet {
		display: flex;
		gap: v.$separacion;
        align-items: center;

		&:nth-child(odd) {
			flex-direction: row-reverse;
		}
	}

	.imagen,
	.texto {
		flex: 1;
	}

	.texto {
		margin-top: 1rem;
		h3 {
			margin: 0;
			text-align: left;
			color: v.$primario;

			&::after {
				content: "";
				display: block;
				width: 50%;
				height: 0.5rem;
				background-color: v.$secundario;
			}
		}
	}
}
