@use "base/variables" as v;
@use "base/mixins" as m;

.contenido-contacto {
	@include m.tablet {
		@include m.grid(2, v.$separacion);
		background-image: url(../img/granos_cafe.jpg);
		background-repeat: no-repeat;
		background-position: right center;
		background-size: 35%;
	}
}

.formulario {
	.campo {
		margin-bottom: 2rem;
		&:last-of-type {
			margin-bottom: 0;
		}
		label {
			display: block;
			text-transform: uppercase;
			font-size: 2.2rem;
			font-weight: 700;
		}
		select,
		input:not([type="submit"]) {
			border: 1px solid v.$primario;
			width: 100%;
			padding: 1rem;
		}
		input:not([type="submit"]):focus {
            outline: none;
			background-color: lighten(v.$primario, 70%);
		}

		input[type="submit"] {
			background-color: v.$blanco;
			color: v.$primario;
			padding: 1rem;
			width: 100%;
			border: 1px solid v.$primario;
			font-size: 2.2rem;
			text-transform: uppercase;
			transition: 0.3s ease-in-out;

			&:hover {
				cursor: pointer;
				background-color: v.$primario;
				color: v.$blanco;
			}
		}
	}
}
