@use "base/variables" as v;
@use "base/mixins" as m;

.footer {
	background-color: v.$primario;

	.contenido-footer {
		padding: 5rem;
		@include m.tablet {
			@include m.grid(3, 2rem);
		}
	}
	.copyright {
		text-align: center;
		color: v.$blanco;
		margin: 0;
		padding: 2rem;
	}
}

.footer-box {
	font-family: v.$fuenteCursiva;
	text-align: center;
	margin-bottom: v.$separacion;
    
	@include m.tablet {
		margin-bottom: 0;
	}

	h3 {
		font-family: v.$fuenteCursiva;
		margin: 0 0 1rem 0;
		font-size: 1.4em;
		color: v.$secundario;
	}
	p,
	a {
		font-size: 2rem;
		margin: 0;
	}
}
